@import url(//db.onlinewebfonts.com/c/2246d642275618faa05b6c551ed5685f?family=CutoffW01-Regular);
@import url(//db.onlinewebfonts.com/c/2246d642275618faa05b6c551ed5685f?family=CutoffW01-Regular);
@import url(//db.onlinewebfonts.com/c/2246d642275618faa05b6c551ed5685f?family=CutoffW01-Regular);
@import url(//db.onlinewebfonts.com/c/2246d642275618faa05b6c551ed5685f?family=CutoffW01-Regular);
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Roboto",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

::-webkit-scrollbar {
  display: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@-ms-viewport{
  width: device-width;
}

.user-navbar{
  background-color:#E5E5E5;
  min-height: -webkit-max-content;
  min-height: -moz-max-content;
  min-height: max-content;
  display: flex;
  min-width: 100%;

}

.usernb-logo{
  height: 80px;
}

.user-link{
  text-decoration: none;
}

.usernb-links{
  align-self: center;
  margin-left: auto;
}

.user-navbar button{
  outline: transparent;
  background-color: transparent;
  border: transparent;
  margin-right: 15px;
  margin-left: 15px;
  
  float: inline-end;
    /* style: display;
   */
}

.user-navbar button h3{
  display: inline;
  font-weight: normal;
  font-size: 22px;
  font-family: 'CutoffW01-Regular';
  /* text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); */
  color:black;
}

.user-navbar button :hover{
  cursor: pointer;
  color: #EB5757;
  /* und */
}
.groupImg{
  width: 100%;
  height: 100vh;
  object-fit: cover;
  opacity: 70%;
  
}
.slideshow{
  width: 100%; 
} 
.rounded-img{
  margin: 1rem;
  border-radius: 15px;
  width: 40rem;

  /* height: min-content */
  /* max-width: 700px; */
  /* height: auto; */
  /* object-fit: cover; */
}

.about-us{
  margin-top: 8%;
  /* min-width: 1000px; */
  width: 100%;
  display: inline-flex
}

.about-us-body{
  /* margin-top: 5%; */
}

.about-us-body p, .meet-teacher-body p{
  margin-top: 5%;
  width: 80%;
  font-weight: lighter;
  font-size: 45px;
  font-style: normal;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  line-height:  2;
  text-align: left;

}

.meet-teacher{
  margin: 6rem;
  text-align: center;
}

.meet-teacher-body{
  /* margin-left: 8%; */
  display: inline-flex
}

.meet-teacher-body p{
  width: 90%;
}

@media only screen and (max-width: 1200px) {
  .meet-teacher-body {
    display: contents;
    text-align: left;
  }
  .rounded-img{
    margin: 0;
    width: 30rem;
    height: auto; 
    object-fit: cover;
  }
}


.subheaderText{
  font-family: 'CutoffW01-Regular';
  font-weight: normal;
  font-size: 45px;
  /* color: #EB5757; */
  color: #ea3b3b;
  margin-bottom: auto;
}

.subheaderLine{
  background-color:#F2994A;
  outline: transparent;
  border: transparent;
  height: 5px;
  float: left;
}

.subheader{
  display: inline-block
}

.headerText{
  font-family: 'CutoffW01-Regular';
  font-weight: normal;
  font-size: 45px;
  color: #EB5757;
  margin: auto;
}

.headerLine{
  background-color:#F2994A;
  outline: transparent;
  border: transparent;
  height: 5px;
  float: left;
}

.header{
  display: inline-block;
}
.contact-us{
  margin-top: 3%;
  margin-left: 3%;
  
}
.email-logo{
  background: #f3f3f3;
  width: 8%;
  min-width: 80px;

}

.phone-logo{
  width: 8%;
  min-width: 80px;

}

.facebook-logo{
  width: 8%;
  min-width: 80px;
}

.email-logo, .phone-logo, .facebook-logo{
  margin: 2%
}

.email-container, .phone-container, .facebook-container{
  display: flex;
  align-items: center;
}

.email-text, .phone-text, .facebook-text {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: 400;
  font-size: 25px;
}

.email-text{
  color: #2D9CDB

}
.phone-text{
  color: #F2994A;
}

.facebook-text{
  color: #2F80ED
}


.gallery-container{
  text-align: -webkit-center;
  margin-top: 2%
}

.react-photo-gallery--gallery{
  margin-top: 2%;
  margin-right: 5%;
  margin-left:5%
}
